* {
  margin: 0;
  padding: 0;
}

html {
  background-color: #202020;
}
ul {
  list-style: none;
}

p {
  margin: 5px 0;
}
.car-container {
  margin: 30px 0 30px 0;
  font-family: "Roboto", Regular;
  font-size: 15px;
  color: #ffffff;
}

.car-container .container {
  width: 1000px;
  height: auto;
  margin: auto;
}

.car-container .container .title {
  width: 600px;
  height: auto;
  text-align: center;
  background-color: #000000;
  padding: 15px 0 15px 0;
  box-sizing: border-box;
  margin: auto;
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 2px;
  box-shadow: 4px 5px 43px 2px rgba(255, 255, 255, 0.08);
}

.car-container .container .create-search {
  display: flex;
  margin: 10px 0;
  flex-wrap: nowrap;
}

.car-container .container .create-search .button {
  font-family: "Roboto", Regular;
  color: #ffffff;
  background: #0d9e36;
  border: 0;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 4px;
}

.car-container .container .create-search .button:hover {
  background: #0d752a;
}

.car-container .container .create-search .search {
  margin-left: auto;
  position: relative;
}

.car-container .container .create-search .search .text {
  background: #202020;
  border: 1px solid #ffffff;
  height: 100%;
  padding: 0 30px 0 10px;
  color: #ffffff;
  border-radius: 4px;
  outline: none;
}

.car-container .container .create-search .search .text::placeholder {
  color: #ffffff;
  opacity: 70%;
}

.car-container .container .create-search .search i {
  padding: 10px;
  position: absolute;
  right: 0;
  top: -2px;
}

.car-container .container .list-content:after {
  content: "";
  display: table;
  clear: both;
}

.car-container .container .list-content .left {
  float: left;
  width: 20%;
  box-sizing: border-box;
}

.car-container .container .list-content .left ul {
  background: #000000;
  margin-right: 20px;
}
.car-container .container .list-content .left ul li {
  padding: 10px 15px;
  border-bottom: 0.5px solid #1f1f1f;
}
.car-container .container .list-content .left ul li:hover {
  background: #292929;
  display: block;
}

.car-container .container .list-content .left ul li a {
  text-decoration: none;
  color: #ffffff;
  display: block;
}

.car-container .container .list-content .right {
  float: left;
  width: 80%;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
}

.car-container .container .list-content .right h2 {
  margin: -5px 0 5px 0;
}

.car-container .container .list-content .right .car {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
  background: #000000;
  padding: 16px;
}

.car-container .container .list-content .right .car:hover {
  box-shadow: 0px 0px 8px 4px #ffffff3d;
}

.car-container .container .list-content .right .car .image {
  width: 40%;
  height: 200px;
}

.car-container .container .list-content .right .car .car-content {
  width: 60%;
  padding-left: 14px;
}

img {
  width: 100%;
  height: 100%;
}

.car-container .container .list-content .car .car-content .edit,
.delete {
  font-family: "Roboto", Regular;
  border: none;
  color: #ffffff;
  padding: 5px 15px;
  margin-right: 10px;
  border-radius: 3px;
  cursor: pointer;
}

.car-container .container .list-content .car .car-content .edit {
  background-color: #2562da;
}

.car-container .container .list-content .car .car-content .delete {
  background-color: #e73939;
}

.car-container .container .number-page ul {
  display: flex;
  justify-content: center;
}

.car-container .container .number-page ul li {
  list-style: none;
  border: 1px solid #ffffff;
  padding: 5px 12px;
  margin: 0 1px;
  cursor: pointer;
}
