* {
  margin: 0;
  padding: 0;
}

.container-main {
  font-family: "roboto", regular;
  width: 500px;
  margin: 90px auto;
  background-color: #202020;
  height: 80vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 8px 33px 58px rgba(255, 255, 255, 0.062);
  border-radius: 7px;
  color: #ffffff;
}

.container-main .container {
  box-sizing: border-box;
  max-width: 500px;
  width: 100%;
  box-sizing: border-box;
  padding: 0 40px;
}

.container-main .container .title {
  text-align: center;
  box-sizing: border-box;
}

.container-main .container .title p {
  border-bottom: 1px solid #ffffff;
  margin: 0px 130px;
  font-size: 24px;
  padding: 5px;
}

.container-main .container .content {
  margin: 20px 0;
}

.container-main .container .content .as {
  margin: 10px 0;
  width: 100%;
}

.container-main .container .content .as .full {
  margin: 5px 0px;
  width: 100%;
  background-color: #202020;
  outline: none;
  border: 1px solid #ffffff;
  padding: 10px 10px;
  color: #ffffff;
  border-radius: 3px;
  box-sizing: border-box;
  resize: none;
}

.container-main .container .footer {
  display: flex;
  justify-content: end;
  flex-wrap: nowrap;
}

.container-main .container .footer button {
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 3px;
  border: none;
  color: #ffffff;
}

.container-main .container .footer .cancel {
  background-color: #202020;
  border: 1px solid #ffffff;
  cursor: pointer;
}

.container-main .container .footer .add {
  background-color: #0d9e36;
  border:1px solid #FFFFFF;
  padding:0 10px;
  border-radius: 3px;
  cursor:pointer;
  color: #ffffff;
}
.container-main .container .footer .add:hover {
  background-color: #0a6d26;
}
